/* Estilos de Dragula */
/* Estilos de draagula */
#drag-elements,
#drag-elements2,
#drag-elements3,
#drag-elements4,
#drag-elements5 {
  display: block;
  /* background-color: #f8f9fa; */
  border-radius: 5px;
  min-height: 150px;
  margin: 0 auto;
  /* padding: 2em; */
  font-family: CharisSILWNormal, sans-serif !important;
}

#drag-elements > div,
#drag-elements2 > div,
#drag-elements3 > div,
#drag-elements4 > div,
#drag-elements5 > div {
  text-align: center;
  float: left;
  padding: 10px;
  margin: 0 1em 1em 0;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
  /* border-radius: 100px; */
  border: 2px solid #ececec;
  border-radius: 5px;
  background: #f7f7f7;
  transition: all 0.5s ease;
  font-family: CharisSILWNormal, sans-serif !important;
}

#drag-elements > div:active,
#drag-elements2 > div:active,
#drag-elements3 > div:active,
#drag-elements4 > div:active,
#drag-elements5 > div:active {
  -webkit-animation: wiggle 0.3s 0s infinite ease-in;
  animation: wiggle 0.3s 0s infinite ease-in;
  opacity: 0.6;
  border: 2px solid #000;
  font-family: CharisSILWNormal, sans-serif !important;
}

#drag-elements > div:hover,
#drag-elements2 > div:hover,
#drag-elements3 > div:hover,
#drag-elements4 > div:hover,
#drag-elements5 > div:hover {
  /* border: 2px solid #f8f9fa; */
  background-color: #e5e5e5;
  cursor: pointer;
  font-family: CharisSILWNormal, sans-serif !important;
}

#drop-target,
#drop-target2,
#drop-target3,
#drop-target4,
#drop-target5 {
  border: 3px dashed #e5e5e5;
  border-radius: 5px;
  min-height: 150px;
  margin: 0 auto;
  /* margin-top: 10px; */
  padding: 1em;
  /* display: block; */
  text-align: center;
  font-family: CharisSILWNormal, sans-serif !important;
  cursor: pointer;
}

#drop-target > div,
#drop-target2 > div,
#drop-target3 > div,
#drop-target4 > div,
#drop-target5 > div {
  transition: all 0.5s;
  text-align: center;
  float: left;
  padding: 1em;
  margin: 0 1em 1em 0;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  /* border: 2px solid skyblue; */
  background: #f7f7f7;
  transition: all 0.5s ease;
  font-family: CharisSILWNormal, sans-serif !important;
  cursor: pointer;
}

#drop-target > div.mal,
#drop-target2 > div.mal,
#drop-target3 > div.mal,
#drop-target4 > div.mal,
#drop-target5 > div.mal {
  transition: all 0.5s;
  text-align: center;
  float: left;
  padding: 10px;
  margin: 0 1em 1em 0;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  /* border: 2px solid skyblue; */
  background: red;
  color: white;
  transition: all 0.5s ease;
  font-family: CharisSILWNormal, sans-serif !important;
