.syllabary {
  table-layout: fixed;
  border: 1em solid #fefefe;
  direction: rtl;
  width: 80%;
}

.syllabary td > div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  text-align: center;
}

.syllabary td .hiragana,
.syllabary td .katakana {
  font-size: 19.6px;
  line-height: 28px;
  height: 28px;
  width: 50%;
  -webkit-transition: background 0.3s;
  -o-transition: background 0.3s;
  transition: background 0.3s;
  background-color: red;
}

.syllabary td .hiragana.over,
.syllabary td .katakana.over {
  -webkit-box-shadow: inset 0 0 0 2px #333;
  box-shadow: inset 0 0 0 2px #333;
}

.syllabary td .hiragana.error,
.syllabary td .katakana.error {
  -webkit-transition: background 0s;
  -o-transition: background 0s;
  transition: background 0s;
  background: #ff6b6b;
}

.syllabary td .romaji {
  line-height: 28px;
