/*Inicio de Memorama */
.memory-game {
  width: 900px;
  width: 1400px;
  height: 600px;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  perspective: 1000px;
  /* margin-bottom: 450px; */
}

.memory-game2 {
  /* width: 900px; */
  width: 1400px;
  height: 600px;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  perspective: 1000px;
  margin-bottom: 450px;
}

.memory-game3 {
  /* width: 900px; */
  width: 1400px;
  height: 600px;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  perspective: 1000px;
  margin-bottom: 650px;
}

.memory-card {
  /* width: calc(25% - 10px); */
  width: calc(15% - 10px);
  height: calc(35% - 10px);
  /* height: calc(33.333% - 10px); */
  margin: 5px;
  position: relative;
  transform: scale(1);
  transform-style: preserve-3d;
  transition: transform 0.5s;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
  cursor: pointer;
}

.memory-card:active {
  transform: scale(0.97);
  transition: transform 0.2s;
}

.memory-card.flip {
  transform: rotateY(180deg);
}

.front-face,
.back-face {
  width: 100%;
  height: 100%;
  padding: 20px;
  position: absolute;
  border-radius: 5px;
  background: #f8f9fa;
  backface-visibility: hidden;
}

.front-face {
  transform: rotateY(180deg);
}

